<template>
  <div class="palyview">
    <audio :src="curentSongUrl" autoplay></audio>
    <template v-if="curentBar">
      <div class="play-bar">
        <div class="play-bar-img" @click="isShowFull=true">
          <img :src="curentBar.al.picUrl" :class="{'paused':isPaused}" />
          <!-- <span class="bb fa fa-expand"></span> -->
        </div>
        <div class="play-bar-ce">
          <p>{{curentBar.name}}</p>
          <p>
            <span v-for="(item, index) in curentBar.ar" :key="index">{{item.name}}</span>
          </p>
        </div>
        <!--  -->
        <span
          class="progress fa fa-play"
          @click.stop="pauseAudio"
          :class="{ 'fa-pause': !isPaused }"
        >
          <canvas width="40px" height="40px"></canvas>
        </span>
      </div>
      <template v-if="isShowFull">
        <div class="play-full" @click.prevent="showList=false">
          <div class="fubg" :style="{'background-image':`url(${curentBar.al.picUrl}`}"></div>
          <span class="proo fa fa-compress" @click="isShowFull=false,ifgeci=false,showList=false"></span>
          <!-- <mark>1212122</mark> -->
          <div class="fu-bar" v-if="!ifgeci">
            <img class="toppic" src="../assets/needle-ip6.png" alt />
            <img
              class="t"
              :src="curentBar.al.picUrl"
              @click="ifgeci=!ifgeci"
              :class="{'paused':isPaused}"
            />
            <h3>{{curentBar.name}}</h3>
            <h3>{{curentBar.ar[0].name}}</h3>
          </div>
          <template v-if="ifgeci">
            <div class="geci" :class="{'scrolling':iftouch}" @click.stop="ifgeci=!ifgeci">
              <h2>{{curentBar.name}}</h2>
              <h3 class="geli">{{curentBar.ar[0].name}}</h3>
              <!-- ========================= -->
              <ul
                @scroll="scroll"
                @touchstart="touchStart"
                @touchmove="touchMove"
                @touchend="touchEnd"
              >
                <li v-for="(item, index) in geci" :key="index">{{item.text}}</li>
              </ul>
            </div>
          </template>
          <div class="kongzhiqi">
            <span v-if="isLoop" @click="changeLoop" class="fa fa-minus-circle"></span>
            <span v-else-if="isSinglecycle" @click="changeSinglecycle" class="fa fa-retweet"></span>
            <span v-else-if="isRandom" @click="changeRandom" class="fa fa-random"></span>

            <span @click="getPreviousSong" class="fa fa-step-backward"></span>
            <span class="fa fa-play" @click="pauseAudio" :class="{ 'fa-pause': !isPaused }"></span>
            <span @click="getNextSong" class="fa fa-step-forward"></span>

            <span @click.stop="showList=!showList" class="fa fa-align-left  "></span>
          </div>
        </div>
      </template>
    </template>
    <ul v-if="showList&&itemObj&&isShowFull" class="showList">
      <li v-for="(item, index) in itemObj" :key="index">{{item.name}}</li>
    </ul>
    <img v-if="loading" src="../assets/timg.gif" alt />
  </div>
</template>

<script>
export default {
  props: ["curentsongId", "showFull"],
  data() {
    return {
      showList: false,
      curentBar: null,
      itemObj: null,
      isPaused: false,
      currentTime: 0,
      geci: null,
      isShowFull: this.showFull || false,
      ifgeci: false,
      timer: true,
      currentIndex: 0,
      loading: false,
      iftouch: false,
      isLoop: true,
      isSinglecycle: false,
      isRandom: false
    };
  },
  computed: {
    curentSongUrl() {
      return (
        "https://music.163.com/song/media/outer/url?id=" +
        this.curentsongId +
        ".mp3"
      );
    }
  },

  methods: {
    getCurentBar() {
      this.loading = true;

      this.axios
        .get("http://music.kele8.cn/song/detail", {
          params: {
            ids: this.curentsongId
          }
        })
        .then(response => {
          window.console.log(response);
          this.curentBar = response.data.songs[0];
          // this.itemObj = this.curentBar;
          localStorage.setItem(
            "curentBar" + this.curentsongId,
            JSON.stringify({
              time: Date.now(),
              data: this.curentBar
            })
          );
        })
        .finally(() => {
          this.getGeCi();
          this.loading = false;
        });
    },
    getGeCi() {
      this.axios
        .get("http://music.kele8.cn/lyric", {
          params: {
            id: this.curentBar.id
          }
        })
        .then(response => {
          window.console.log(response);
          this.geci = response.data.lrc.lyric;
          let arr = [];
          this.geci.split("\n").forEach((element, index) => {
            var t = element.slice(1, 10);
            arr[index] = {
              time: Number(t.slice(0, 2)) * 60 + Number(t.slice(3)),
              text: element.slice(11).trim()
            };
          });
          arr.pop();
          this.geci = arr;
        });
    },
    pauseAudio() {
      let audio = this.$el.querySelector("audio");
      if (this.isPaused) {
        audio.play();
      } else {
        audio.pause();
      }
      this.isPaused = audio.paused;
    },
    drawCircle() {
      let audio = this.$el.querySelector("audio");
      let canvas = this.$el.querySelector("canvas");
      let ctx = canvas.getContext("2d");

      let id = window.setInterval(() => {
        this.currentTime = audio.currentTime * 1000;

        ctx.clearRect(0, 0, 40, 40);
        //
        ctx.beginPath();
        ctx.arc(20, 20, 19, 0, 2 * Math.PI);
        ctx.closePath();
        ctx.strokeStyle = "#ccc";
        ctx.stroke();
        //
        ctx.beginPath();
        ctx.arc(
          20,
          20,
          19,
          -0.5 * Math.PI,
          2 * Math.PI * (this.currentTime / this.curentBar.dt) - 0.5 * Math.PI
        );
        ctx.strokeStyle = "red";
        ctx.stroke();
      }, 500);
      return id;
    },
    geciscroll() {
      // let audio = this.$el.querySelector("audio");
      let geciBox = document.querySelector(".geci ul");
      let geci = this.geci;

      let lis = document.querySelectorAll(".geci ul li");
      let currentTime = this.currentTime / 1000;

      // window.console.log(currentTime);

      // 
      for (let i = 0; i < geci.length; i++) {
        if (geci[i].time >= currentTime) {
          //   
          this.currentIndex = i;
          break;
        }
      }
      // 
      lis.forEach((element, index) => {
        // console.log(index, currentIndex)
        if (index == this.currentIndex) {
          if (index < 1) {
            lis[0].classList.add("active");
          } else if (index == geci.length - 1) {
            lis[index].classList.add("active");
          } else {
            lis[index - 1].classList.add("active");
          }
        } else {
          lis[index].classList.remove("active");
        }
      });

      //   
      let listop = lis[this.currentIndex].offsetTop;
      let boxtop = geciBox.offsetTop;

      if (listop - boxtop > 220) {
        geciBox.scrollTo({
          top: listop - boxtop - 220,
          behavior: "smooth"
        });
      }

      //
    },
    //
    scroll(event) {
      event.preventDefault();
    },
    touchStart() {
      this.timer = false;
    },
    touchMove() {
      let geciBox = document.querySelector(".geci ul");

      this.currentIndex = Math.round(geciBox.scrollTop / 20 + 1);
      window.console.log(this.currentIndex);
      this.iftouch = true;
    },
    touchEnd() {
      let audio = this.$el.querySelector("audio");

      this.timer = true;
      audio.currentTime = this.geci[this.currentIndex - 1].time;
      this.geci;
      this.iftouch = false;
    },

    getNextSong() {
      this.audio = document.querySelector("audio");
      if (this.isRandom) {
        window.console.log("getNextSong");
        this.getRandomSong();
      } else {
        let audio = document.querySelector("audio");
        let MusicList = localStorage.getItem("MusicList");

        let MusicListString = JSON.parse(MusicList).data;
        window.console.log("MusicListString", MusicListString);
        for (let index = 0; index < MusicListString.length; index++) {
          for (const key in MusicListString[index]) {
            if (
              MusicListString[index][key] == this.curentBar.id &&
              index < MusicListString.length - 1
            ) {
              this.axios
                .get("https://music.kele8.cn/song/detail", {
                  params: {
                    ids: MusicListString[index + 1].id
                  }
                })
                .then(response => {
                  this.curentBar = response.data.songs[0];
                  // window.console.log(audio.src);
                  audio.src =
                    "https://music.163.com/song/media/outer/url?id=" +
                    MusicListString[index + 1].id +
                    ".mp3";
                });
            }
          }
        }
      }
    },
    getPreviousSong() {
      let MusicList = localStorage.getItem("MusicList");
      let MusicListString = JSON.parse(MusicList).data;
      let audio = document.querySelector("audio");
      for (let index = 0; index < MusicListString.length; index++) {
        for (const key in MusicListString[index]) {
          if (MusicListString[index][key] == this.curentBar.id && index > 0) {
            // index = index < 0  ? MusicListString.length - 1 : --index;
            this.axios
              .get("https://music.kele8.cn/song/detail", {
                params: {
                  ids: MusicListString[index - 1].id
                }
              })
              .then(response => {
                this.curentBar = response.data.songs[0];
                audio.src =
                  "https://music.163.com/song/media/outer/url?id=" +
                  MusicListString[index - 1].id +
                  ".mp3";
              });
          }
        }
      }
    },
    getRandomSong() {
      let MusicList = localStorage.getItem("MusicList");
      let MusicListString = JSON.parse(MusicList).data;
      let audio = document.querySelector("audio");
      let index = Math.floor(Math.random() * (MusicListString.length - 1));
      this.axios
        .get("https://music.kele8.cn/song/detail", {
          params: {
            ids: MusicListString[index].id
          }
        })
        .then(response => {
          this.curentBar = response.data.songs[0];
          audio.src =
            "https://music.163.com/song/media/outer/url?id=" +
            MusicListString[index].id +
            ".mp3";
        });
    },
    changeLoop() {
      let audio = document.querySelector("audio");
      this.isLoop = false;
      this.isSinglecycle = true;
      audio.loop = true;
    },
    changeSinglecycle() {
      let audio = document.querySelector("audio");
      this.isSinglecycle = false;
      this.isRandom = true;
      audio.loop = false;
      audio.onended = () => {
        this.getRandomSong();
      };
    },
    changeRandom() {
      let audio = document.querySelector("audio");
      this.isRandom = false;
      this.isLoop = true;
      audio.onended = () => {
        this.getNextSong();
      };
    }
  },
  created() {
    this.getCurentBar();
  },
  watch: {
    curentsongId: function() {
      this.getCurentBar();
      this.isShowFull = this.showFull;
    },
    isPaused() {
      if (this.isPaused) {
        window.clearInterval(window.animateId);
      } else {
        window.clearInterval(this.animateId);
        window.animateId = this.drawCircle();
      }
    },
    curentBar() {
      this.$nextTick(function() {
        window.clearInterval(this.animateId);
        window.animateId = this.drawCircle();
      });
      this.getGeCi();
      let MusicList = localStorage.getItem("MusicList");
      this.itemObj = JSON.parse(MusicList).data;
    },
    currentTime() {
      if (this.ifgeci && this.timer) {
        this.geciscroll();

        // this.aa();
      }
    }
  },
  updated() {}
};
</script>

<style lang='less'>
audio {
  position: fixed;
  bottom: 30vh;
  left: 0;
  width: 100%;
  height: 70px;
}
.palyview {
  position: fixed;
  bottom: 0;
  left: 0;
  background-color: aliceblue;
  width: 100vw;
  z-index: 10;
  .play-bar {
    display: flex;
    border: 1px solid #cccccc;
    height: 55px;
    padding: 10px;
    .play-bar-img {
      position: relative;
      img {
        width: 40px;
        height: 40px;
        border-radius: 50%;
        animation: myroll 10s linear infinite;
        &.paused {
          animation-play-state: paused;
        }
      }
    }
    .play-bar-ce {
      flex: 1;
      margin: 0 10px;

      p {
        line-height: 20px;
        &:nth-child(2) {
          font-size: 12px;
        }
      }
    }

    @keyframes myroll {
      from {
        transform: rotate(0);
      }
      to {
        transform: rotate(360deg);
      }
    }

    .progress {
      width: 40px;
      height: 40px;
      text-align: center;
      line-height: 40px;
      color: #999999;
      position: relative;
      canvas {
        position: absolute;
        top: 0;
        left: 0;
      }
    }
    .proo {
      width: 40px;
      height: 40px;
      text-align: center;
      line-height: 40px;
      font-size: 28px;
      color: #999999;
    }
  }
  .play-full {
    width: 100vw;
    height: 100vh;
    color: white;
    position: relative;
    &::after {
      content: "";
      display: block;
      width: 100%;
      height: 100%;
      background-color: rgba(0, 0, 0, 0.3);
      position: absolute;
      top: 0;
      left: 0;
      z-index: -1;
    }
    span {
      color: #ffffff;
      // line-height: 30px;
      padding: 10px;
      font-size: 30px;
      position: relative;
      z-index: 99;
    }
    .fubg {
      position: absolute;
      bottom: 0;
      left: 0;
      background-position: center center;
      background-size: cover;
      filter: blur(10px);
      width: 100%;
      height: 100%;
      z-index: -1;
    }
    .fu-bar {
      margin-top: 30px;
      display: flex;
      flex-direction: column;
      align-items: center;
      img.toppic {
        width: 94px;
        position: absolute;
        top: 0;
        left: 50%;
        z-index: 9;
        transform: translateX(-15%);
        //
      }
      img.t {
        width: 65vw;
        height: 65vw;
        border-radius: 50%;
        border: 10vw solid #333;
        animation: myroll 5s linear infinite;
        box-shadow: 0 0 0 10px rgba(255, 255, 255, 0.3);
        &.paused {
          animation-play-state: paused;
        }
      }

      @keyframes myroll {
        from {
          transform: rotate(0);
        }
        to {
          transform: rotate(360deg);
        }
      }
      h3 {
        text-align: center;
        font-size: 26px;
        margin: 20px 10px;
      }
    }
    .kongzhiqi {
      width: 100%;
      padding-bottom: 10px;
      position: absolute;
      bottom: 0;
      left: 0;
      display: flex;
      align-items: center;
      justify-content: space-around;
      // background-color: rgba(0, 0, 0, );
      height: 60px;
      span {
        font-size: 40px;
      }
    }
  }
  ul.showList {
    width: 60vw;
    height: 60vh;
    padding: 5px;
    position: fixed;
    right: 0;
    bottom: 60px;
    background: black;
    color: rgba(255, 255, 255, 0.561);
    overflow: hidden;
    overflow: scroll;
    z-index: 9;
    li {
      width: 100%;
      line-height: 30px;
    }
  }
}
.geci {
  width: 100%;
  // overflow-y: scroll;
  position: relative;
  top: 0;
  left: 0;
  text-align: center;
  display: flex;
  align-items: center;
  flex-direction: column;
  height: 80vh;

  h3,
  h2 {
    font-size: 18px;
    line-height: 30px;
    &.geli {
      font-size: 16px;

      padding-bottom: 20px;
    }
  }

  &.scrolling {
    &::before {
      content: "";
      display: block;
      position: absolute;
      width: 100%;
      top: 40%;
      /* margin-top: 10px; */
      left: 0;
      border-top: 1px dashed rgb(195, 191, 191);
    }
  }

  ul {
    overflow-y: scroll;
    padding: 20vh 0;
  }

  li {
    padding: 0 50px;
    text-align: center;
    line-height: 25px;
    font-size: 14px;

    &.active {
      color: orange;
    }
  }
}
</style>